/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	PlainText,
	InspectorControls,
} from '@wordpress/block-editor';
import { PanelBody, Flex, FlexItem, RadioControl, ToggleControl, PanelRow, TextControl  } from '@wordpress/components';
import { getSetting } from '@woocommerce/settings';
import {
	CheckboxControl,
} from '@woocommerce/blocks-checkout';
import { useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import './style.scss';

const { defaultText, defaultIndividualLabel, defaultCompanyLabel, defaultVatNumberLabel } = getSetting( 'wc-szamlazz-vat-number_data', '' );

export const Edit = ( { attributes, setAttributes } ) => {
	const { text, individualLabel, companyLabel, vatNumberLabel, fieldType, autoComplete, companyDefault, showTitle, checkboxLabel } = attributes;
	const blockProps = useBlockProps();
	const [ orderTypeCompany, setOrderTypeCompany ] = useState( true );
	return (
		<div { ...blockProps }>
				
			<InspectorControls key="setting">
				<PanelBody title={ __('Settings', 'wc-szamlazz') }>							
					<PanelRow>
						<RadioControl
							label={__('Field Type', 'wc-szamlazz')}
							selected={ fieldType }
							options={ [
								{ label: __( 'Radio buttons', 'wc-szamlazz' ), value: 'radio' },
								{ label: __( 'Single checkbox', 'wc-szamlazz' ), value: 'checkbox' },
							] }
							onChange={ (value) => setAttributes( { fieldType: value } ) }
						/>
					</PanelRow>
					{ fieldType == 'checkbox' &&
						<PanelRow>
							<fieldset>
								<TextControl
									label={ __( 'Checkbox label', 'wc-szamlazz' ) }
									value={ checkboxLabel }
									onChange={ (value) => setAttributes( { checkboxLabel: value } ) }
									help={ __(
										'Text to display next to the checkbox',
										'wc-szamlazz'
									) }
								/>
							</fieldset>
						</PanelRow>
					}
					{ fieldType == 'radio' &&
						<PanelRow>
							<fieldset>
								<ToggleControl
									label={__( 'Company by default', 'wc-szamlazz' )}
									onChange={ () => setAttributes( { companyDefault: ! companyDefault } ) }
									checked={ companyDefault }
								/>
							</fieldset>
						</PanelRow>
					}
					<PanelRow>
						<ToggleControl
							label={__( 'Show Title', 'wc-szamlazz' )}
							onChange={ () => setAttributes( { showTitle: ! showTitle } ) }
							checked={ showTitle }
						/>
					</PanelRow>
					<PanelRow>
						<ToggleControl
							label={__( 'Autocomplete', 'wc-szamlazz' )}
							onChange={ () => setAttributes( { autoComplete: ! autoComplete } ) }
							checked={ autoComplete }
							help={ __(
								'If the customer enters a VAT number, it will validate it and also prefill the address and company name fields automatically.',
								'wc-szamlazz'
							) }
						/>
					</PanelRow>
				</PanelBody>
			</InspectorControls>

			<div class="wc-block-components-radio-control">
				{ showTitle &&
					<p>
						<PlainText
						className={ '' }
						value={ text || defaultText }
						onChange={ ( value ) => setAttributes( { text: value } ) }
						style={ { backgroundColor: 'transparent' } }
						/>
					</p>
				}

				{ fieldType == 'radio' &&
					<div className='wc-block-components-radio-control wc-szamlazz-vat-number-block-radios'>
						{['individual', 'company'].map((customerType) => (
							<label className={`wc-block-components-radio-control__option ${'company' === customerType? 'wc-block-components-radio-control__option-checked': ''}`} htmlFor={`checkout-customer-type-${customerType}`}>
								<input
								id={`checkout-customer-type-${customerType}`}
								class="wc-block-components-radio-control__input"
								name="radio-control-wc-billing-customer-type"
								type="radio"
								value={customerType}
								checked={'company' === customerType}
								/>
								<div class="wc-block-components-radio-control__option-layout">
								<div class="wc-block-components-radio-control__label-group">
									<span class="wc-block-components-radio-control__label">
									{customerType === 'individual' && (
										<PlainText
											value={ individualLabel || defaultIndividualLabel }
											onChange={ ( value ) => setAttributes( { individualLabel: value } ) }
											style={ { backgroundColor: 'transparent' } }
										/>
									) }

									{customerType === 'company' && (
										<PlainText
											value={ companyLabel || defaultCompanyLabel }
											onChange={ ( value ) => setAttributes( { companyLabel: value } ) }
											style={ { backgroundColor: 'transparent' } }
										/>
									) }
									</span>
								</div>
								</div>
							</label>
						))}
						</div>
					} 

					{ fieldType == 'checkbox' &&
						<CheckboxControl
							className="wc-szamlazz-vat-number-checkbox"
							label={ checkboxLabel }
							onChange={ ( isChecked ) => {
								setOrderTypeCompany( isChecked );
							} }
							checked={orderTypeCompany}
						/>
					}

					{ (orderTypeCompany || fieldType == 'radio') &&
						<div class="wc-block-components-text-input">
							<input type="text"/>
							<label>
								<PlainText
									value={ vatNumberLabel || defaultVatNumberLabel }
									onChange={ ( value ) => setAttributes( { vatNumberLabel: value } ) }
								/>
							</label>
						</div>
					}
			</div>
			
		</div>
	);
};

export const Save = () => {
	return <div { ...useBlockProps.save() } />;
};
